@charset "UTF-8";
/* ================================
	Frontend CSS.

	Index:
	r01 - Placeholders
	r02 - Global
	r03 - Utilities
	r04 - Components
	r05 - Layouts
===================================*/
.rt-d-flex {
  display: flex;
}

.rt-flex-wrap {
  flex-wrap: wrap;
}

.rt-justify-content-center {
  justify-content: center;
}

.rt-align-items-center {
  align-items: center;
}

.rt-absolute-content, .rt-relative-content, .rt-static-content {
  position: relative;
}

.rt-primary-color {
  color: var(--color-primary);
}

.rt-primary-color-bg {
  background-color: var(--color-primary);
}

.rt-white-color {
  color: var(--color-white);
}

.rt-white-color-bg {
  background-color: var(--color-white);
}

.rt-black-color {
  color: var(--color-body);
}

.rt-black-color-bg {
  background-color: var(--color-body);
}

.rt-mb-0 {
  margin-bottom: 0;
}

/**
 * Global CSS.
 */
:root {
  --color-primary: #FFC100;
  --color-secondary: #030100;
  --color-secondary-100: #FCFAF4;
  --color-gray: #F5F5F5;
  --color-white: #fff;
  --color-body: #4B4B4B;
  --color-black: #111111;
  --color-border: #E3E3E3;
  --color-font: #696969;
  --font-body: "Poppins", sans-serif;
  --font-title: "Poppins", sans-serif;
  --font-banner: "Days One", sans-serif;
  --border-radius-xs: 4px;
  --border-radius-sm: 6px;
  --border-radius-md: 10px;
  --border-radius-lg: 15px;
  --border-radius-xl: 20px;
}

/* ===========================
// Typography
==============================*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: 16px;
  color: var(--color-body);
  font-weight: 400;
}

p {
  font-family: var(--font-title);
  font-size: 16px;
  color: var(--color-body);
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 15px;
}

/*==== heading fonts setup ====*/
h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--color-black);
  font-family: var(--font-title);
  line-height: 1.2;
  font-weight: 600;
}

h1 {
  font-size: 48px;
  line-height: 1.23;
}
@media (max-width: 767px) {
  h1 {
    font-size: 36px;
  }
}

h2 {
  font-size: 40px;
}

h3 {
  font-size: 32px;
}

h4 {
  font-size: 26px;
}

h5 {
  font-size: 22px;
}

h6 {
  font-size: 18px;
}

