/*==== text color & bg color ====*/
/*============================ 
// Global styles
=============================*/
html {
  overflow-x: hidden !important;
  width: 100%;
  height: 100%;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
  vertical-align: baseline;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  position: relative;
  background-color: var(--color-white);
  transition: all 0.3s ease-in-out;
  z-index: 2;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: var(--color-primary);
  display: inline-block;
  transition: all 0.3s ease-in-out;
}
a:link, a:visited, a:focus {
  text-decoration: none;
  outline: 0;
}
a:hover {
  text-decoration: none;
  color: var(--color-primary);
}

img {
  max-width: 100%;
  height: auto;
}

input:focus,
textarea:focus,
select:focus {
  outline: 0;
  box-shadow: none;
}

.section-padding {
  padding-block: 120px;
}
@media (max-width: 1199px) {
  .section-padding {
    padding: 100px 0;
  }
}
@media (max-width: 991px) {
  .section-padding {
    padding: 80px 0;
  }
}

section,
.section {
  position: relative;
}

@media (min-width: 1400px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl {
    max-width: 1140px;
  }
}
/**
 * Utility CSS.
 */
/*======================================
// pre-loader
=====================================*/
#preloader {
  background: url("../../media/preloader.gif") rgba(255, 255, 255, 0.99) no-repeat scroll center center;
  height: 100%;
  left: 0;
  overflow: visible;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999999;
}

/*======================================
// Back-to-top
=====================================*/
button.scroll-to-top {
  background-color: var(--color-primary);
  height: 0px;
  width: 0px;
  overflow: hidden;
  border-radius: 50px;
  color: transparent;
  clear: both;
  visibility: hidden;
  position: fixed;
  cursor: pointer;
  display: block;
  border: none;
  right: 50px;
  bottom: 75px;
  outline: 0 !important;
  z-index: 99;
  transition: all 0.3s ease-in-out;
}
button.scroll-to-top i::before {
  font-size: 10px;
  color: var(--color-white);
  position: relative;
  top: -2px;
}

button.scroll-to-top.show {
  display: block;
  background-color: var(--color-primary);
  right: 25px;
  bottom: 50px;
  height: 40px;
  width: 40px;
  visibility: visible;
  box-shadow: 6px 6px 12px rgba(181, 98, 0, 0.16);
}
button.scroll-to-top.show:hover i::before {
  font-size: 10px;
  animation: toBottomFromTop 0.7s forwards;
}
button.scroll-to-top.show i {
  color: var(--color-white);
}

button.scroll-to-top.show:active {
  box-shadow: 6px 6px 12px rgba(181, 98, 0, 0.16);
}

.rt-icon::before {
  width: auto;
  margin-left: 0;
  margin-right: 0;
  line-height: unset;
}

.rt-social ul {
  gap: 13px;
}
.rt-social ul li a i::before {
  color: var(--color-body);
  transition: all 0.3s ease-in-out;
}
.rt-social ul li a:hover i::before {
  color: var(--color-secondary);
}

.rt-social-box {
  margin-top: 15px;
}
@media only screen and (min-width: 480px) {
  .rt-social-box {
    position: absolute;
    top: 30px;
    right: 20px;
    margin-top: 0;
  }
}
.rt-social-box ul {
  gap: 6px;
}
.rt-social-box ul li a {
  height: 36px;
  width: 36px;
  border-radius: 100%;
  background: #F8F8F8;
  border: 1px solid #DCDCDC;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
}
.rt-social-box ul li a i::before {
  color: var(--color-body);
  transition: all 0.3s ease-in-out;
}
.rt-social-box ul li a:hover {
  background-color: var(--color-secondary);
  border-color: var(--color-secondary);
}
.rt-social-box ul li a:hover i::before {
  color: var(--color-white);
}
.rt-social-box.style-2 {
  position: absolute;
  top: 30px;
  right: 40px;
}
@media only screen and (min-width: 768px) {
  .rt-social-box.style-2 {
    top: 50%;
    transform: translateY(-50%);
    right: 30px;
  }
}
.rt-social-box.style-2 ul li {
  margin-bottom: 10px;
}
.rt-social-box.style-2 ul li:last-child {
  margin-bottom: 0;
}
.rt-social-box.style-3 {
  position: unset;
}
.rt-social-box.style-3 ul li a {
  border-radius: var(--border-radius-sm);
  background-color: transparent;
}
.rt-social-box.style-3 ul li a:hover {
  background-color: var(--color-secondary);
}

/**
 * Components: Input.
 */
.input-group {
  width: 100%;
  position: relative;
}
.input-group .form-control {
  border-radius: 8px !important;
  height: 70px;
  width: 100%;
  font-size: 16px;
  line-height: 28px;
  color: var(--color-primary);
  padding: 0 0 0 20px;
  background-color: var(--color-white);
  color: var(--color-primary);
  border: 1px solid #000;
}
.input-group .form-control:focus {
  outline: 0;
  box-shadow: none;
  border-color: var(--color-white);
  background-color: transparent;
}
.input-group .form-control::placeholder {
  color: #696969;
}
.input-group .btn-outline-secondary {
  border: 0;
  background-color: var(--color-secondary);
  border-radius: var(--border-radius-sm) !important;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease-in-out;
  padding: 13px 40px 10px;
  z-index: 4;
  color: var(--color-white);
  font-size: 14px;
  font-weight: 600;
  height: 50px;
  margin-top: 5px;
}
@media only screen and (min-width: 576px) {
  .input-group .btn-outline-secondary {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 11px;
    margin-top: 0;
  }
}
.input-group .btn-outline-secondary i {
  margin-left: 5px;
}
.input-group .btn-outline-secondary i::before {
  color: var(--color-white);
}
.input-group .btn-outline-secondary:hover {
  background-color: var(--color-black);
  color: var(--color-white);
}
@media only screen and (min-width: 576px) {
  .input-group .btn-outline-secondary:hover {
    background-color: var(--color-primary);
    color: var(--color-white);
  }
}
.input-group .btn-outline-secondary:focus {
  outline: 0;
  box-shadow: none;
}

.rt-brand-wrap {
  padding: 70px 0;
}

.rt-brand-box {
  cursor: pointer;
}
@media only screen and (min-width: 1200px) {
  .rt-brand-box {
    cursor: pointer;
    height: 60px;
    width: 100%;
    display: inline-flex;
    align-items: center;
    line-height: 60px;
  }
}
.rt-brand-box:hover .rt-brand-img img {
  transform: scale(1.02);
}

.rt-brand-img img {
  transition: all 0.3s ease-in-out;
}

/**
 * Components: Blog.
 */
.rt-blog-wrap {
  background-color: var(--color-secondary-100);
}

.rt-blog-box:hover .item-img a img {
  transform: translateY(10px) scale(1.1);
}
.rt-blog-box .item-img {
  border-radius: 15px 15px 0 0;
}
@media only screen and (max-width: 1199px) {
  .rt-blog-box .item-img a {
    width: 100%;
  }
}
.rt-blog-box .item-img a img {
  border-radius: 15px 15px 0 0;
  transition: all 0.3s ease-out 0s;
  width: 100%;
}
.rt-blog-box .item-content {
  padding: 22px 22px 30px 28px;
  background-color: var(--color-white);
  border-radius: 0 0 15px 15px;
}
.rt-blog-box .rt-title {
  font-size: 20px;
  font-weight: 600;
  line-height: 26px;
  text-transform: capitalize;
  margin-bottom: 15px;
}
.rt-blog-box .rt-title a {
  color: var(--color-black);
  transition: all 0.3s ease-in-out;
}
.rt-blog-box .rt-title a:hover {
  color: var(--color-primary);
}

.rt-tag .tag {
  color: var(--color-secondary);
  font-size: 14px;
  font-weight: 500;
  background-color: var(--color-primary);
  border-radius: var(--border-radius-xs);
  display: inline-block;
  padding: 8px 12px;
  transition: all 0.3s ease-in-out;
  font-family: var(--font-title);
  line-height: 1;
  text-transform: capitalize;
}
.rt-tag .tag:hover {
  color: var(--color-white);
  background-color: var(--color-secondary);
}

.entry-meta-list {
  gap: 15px;
}
@media only screen and (min-width: 321px) {
  .entry-meta-list {
    gap: 27px;
  }
}
.entry-meta-list a {
  color: var(--color-body);
  transition: all 0.3s ease-in-out;
  font-size: 15px;
  font-family: var(--font-title);
}
.entry-meta-list a i {
  margin-right: 5px;
  position: relative;
  top: 1px;
}
.entry-meta-list a i::before {
  color: var(--color-primary);
  transition: all 0.3s ease-in-out;
}
.entry-meta-list a:hover {
  color: var(--color-secondary);
}
.entry-meta-list a:hover i::before {
