/*========================================
//  main-menu
==========================================*/
/*rt-main-menu*/
.rt-main-menu .menu {
  display: flex;
  align-items: center;
}
.rt-main-menu .menu .menu-item {
  position: relative;
}
.rt-main-menu .menu .menu-item .menu-link {
  display: inline-block;
  font-family: var(--font-title);
  position: relative;
  font-weight: 500;
  font-size: 15px;
  color: var(--color-white);
  transition: all 0.4s ease-in-out;
}
.rt-main-menu .menu .menu-item .menu-link .shape {
  margin-left: 5px;
}
.rt-main-menu .menu .menu-item .menu-link .shape i::before {
  color: var(--color-white);
  font-size: 7px;
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.rt-main-menu .menu .menu-item .menu-link:hover {
  color: var(--color-primary);
}
.rt-main-menu .menu .menu-item .menu-link:hover .shape i::before {
  color: var(--color-primary);
  transform: translateY(1.5px);
}

.rt-main-menu .menu .menu-item:hover.menu-item-has-children::after {
  border-color: var(--color-primary);
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu {
  background-color: #ffffff;
  min-width: 260px;
  visibility: hidden;
  position: absolute;
  top: 100px;
  left: 0;
  box-shadow: 0 13px 48px 0 rgba(0, 0, 0, 0.15);
  padding: 10px 0;
  opacity: 0;
  transform-origin: 0 0 0;
  transform: scaleY(0);
  transition: all 0.3s ease-in-out;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .menu-item {
  bottom: 0;
  width: 100%;
  position: relative;
  transition: all 0.3s ease-in-out;
  margin: 10px 0;
}
.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .menu-item:hover .child-menu::before {
  width: 7px;
  visibility: visible;
  opacity: 1;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu {
  overflow: hidden;
  font-size: 14px;
  padding: 2px 20px;
  display: block;
  position: relative;
  transition: all 0.3s ease-in-out;
  color: var(--color-secondary);
  font-weight: 500;
}
.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu::before {
  content: "";
  height: 100%;
  top: 50%;
  transform: translateY(-50%);
  content: "";
  position: absolute;
  left: 20px;
  right: 20px;
  width: 0;
  height: 2px;
  background: var(--color-primary);
  transition: all 0.3s ease-in-out;
  visibility: hidden;
  opacity: 0;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .menu-item:last-child .child-menu {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: 0;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu:hover {
  color: var(--color-primary);
  padding-left: 35px;
}

.rt-main-menu .menu .menu-item:hover > .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: scaleY(1);
}

.rt-main-menu .menu > .menu-item {
  margin-right: 30px;
}

.rt-main-menu .menu > .menu-item > a {
  padding: 40px 0 37px;
}

.rt-main-menu .menu > .menu-item:last-child {
  margin-right: 0;
}

/*=====================================
  // rt mobile menu
======================================*/
.offscreen-navigation .menu {
  font-family: var(--font-title);
}

.offscreen-navigation .menu > li {
  border-bottom: 1px solid #e9e9e9;
}

.offscreen-navigation .menu > li:last-child {
  border-bottom: 0;
}

.offscreen-navigation .menu > li > a {
  background-color: #f3f3f3;
  font-size: 16px;
  padding: 12px 30px;
  transition: all 0.3s ease-in-out;
}

.offcanvas-content-style-2 .offscreen-navigation .menu > li > a {
  background-color: transparent;
  padding: 12px 0;
}

.offscreen-navigation .menu > li > a.opened {
  background-color: #f8f8f8;
}

.offscreen-navigation .sub-menu {
  display: none;
  background-color: #ffffff;
}

.offscreen-navigation .sub-menu li.menu-item-has-children > a::after,
.offscreen-navigation .sub-menu li.menu-item-has-children > a::before {
  right: 40px;
}

.offscreen-navigation .sub-menu li a {
  border-top: 1px solid #f1f1f1;
  padding: 7px 40px;
  transition: all 0.3s ease-in-out;
  font-size: 15px;
  padding: 6px 17px;
  color: var(--color-black);
  font-weight: 500;
  overflow: hidden;
  position: relative;
  z-index: 1;
  display: flex;
  line-height: 30px;
  transition: 500ms;
}

.offcanvas-content-style-2 .offscreen-navigation .sub-menu li a {
  padding-left: 30px;
}

.offscreen-navigation .sub-menu li .sub-menu li a {
  padding: 7px 50px;
}

.offscreen-navigation ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.offscreen-navigation ul li > a {
  font-size: 16px;
  padding: 6px 17px;
  color: var(--color-black);
  font-weight: 600;
  overflow: hidden;
  position: relative;
  z-index: 1;
  display: flex;
  line-height: 30px;
  transition: all 0.4s ease-in-out;
}
.offscreen-navigation ul li > a .overlay {
  display: block;
  position: absolute;
  width: 100%;
  height: 0%;
  top: auto;
  left: 0%;
  bottom: 0%;
  background-color: var(--color-secondary);
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease-in-out;
}
.offscreen-navigation ul li > a:hover .overlay {
  height: 100%;
  top: 0%;
  bottom: auto;
  visibility: visible;
  opacity: 1;
}

.offscreen-navigation ul li.menu-item-has-children > a:after {
  color: var(--color-body);
  content: "\f067";
  display: inline-block;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  right: 20px;
  position: absolute;
  top: 50%;
  font-size: 16px;
  opacity: 1;
  visibility: visible;
  transform: translateY(-50%) scale(1);
  transition: 500ms;
}

.offscreen-navigation ul li.menu-item-has-children > a:before {
  color: var(--color-body);
  content: "\f068";
  display: inline-block;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  right: 20px;
  position: absolute;
  font-size: 16px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
  transform: translateY(-50%) scale(0);
  transition: 500ms;
  top: 50%;
}

.offscreen-navigation ul li.menu-item-has-children > a.opened:after {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-50%) scale(0);
}

.offscreen-navigation ul li.menu-item-has-children > a.opened:before {
  opacity: 1;
  visibility: visible;
  transform: translateY(-50%) scale(1);
}

.rt-slide-nav {
  position: absolute;
  background: #fff;
  width: 100%;
  left: 0;
  display: none;
  max-height: calc(100vh - 100px);
  overflow-y: auto;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
  top: 100%;
  z-index: 99;
}

.rt-mobile-menu {
